body {
  background: #212339;
  color: white;
  font-family: "Roboto Mono", monospace;
  margin-top: 8rem;
}
body .container {
  max-width: 960px;
  margin: 0 auto;
  text-align: center;
}
body .container #app .connect-wrapper .message {
  color: #00ca94;
  font-size: 14px;
}
body .container #app .connect-wrapper button.connect {
  background: #6970d6;
  border: 0;
  box-shadow: #9ea3e5 4px 4px 0px;
  color: white;
  opacity: 0.8;
  text-transform: uppercase;
  font-size: 11px;
  font-weight: 300;
  padding: 4px 6px;
  border-radius: 4px;
  cursor: pointer;
}
body .container #app .poll-container .title {
  opacity: 0.8;
  margin-bottom: 0;
}
body .container #app .poll-container .sub-title {
  margin-top: 0;
  font-size: 16px;
  opacity: 0.3;
  margin-bottom: 2rem;
  font-weight: 300;
}
body .container #app .poll-container .poll .options input[type=radio] {
  display: none;
}
body .container #app .poll-container .poll .options label {
  display: inline-block;
  background-color: #6970d6;
  opacity: 0.9;
  padding: 4px 11px;
  font-family: Arial;
  font-size: 16px;
  cursor: pointer;
  margin: 0 16px;
  border-radius: 4px;
  box-shadow: #9ea3e5 4px 4px 0px;
  transition: background-color 0.2s ease-in-out;
}
body .container #app .poll-container .poll .options label:hover {
  background-color: #4d55ce;
  transition: background-color 0.2s ease-in-out;
}
body .container #app .poll-container .poll .options label img.smiley {
  width: 16px;
  height: 16px;
  margin: 0px 4px -3px 0px;
}
body .container #app .poll-container #spinner {
  margin-top: 2rem;
}
body .container #app .poll-container #spinner.idle {
  visibility: hidden;
}
body .container #app .poll-container #spinner.done .wave-wrapper {
  visibility: hidden;
}
body .container #app .poll-container #spinner.done .confirmation {
  visibility: visible;
}
body .container #app .poll-container #spinner.proccesing .wrapper {
  visibility: visible;
}
body .container #app .poll-container #spinner .wave-wrapper .text {
  opacity: 0.8;
  color: #00ca94;
}
body .container #app .poll-container #spinner .wave-wrapper .wrapper {
  margin: auto;
}
body .container #app .poll-container #spinner .confirmation {
  opacity: 0.8;
  visibility: hidden;
}
body .container #app .poll-container .history {
  margin-top: 8rem;
}
body .container #app .poll-container .history .title {
  opacity: 0.8;
  margin-bottom: 2rem;
  font-weight: 300;
}
body .container #app .poll-container .history .items .row {
  margin-bottom: 4px;
  display: grid;
  grid-template-columns: 10% 40% 50%;
  padding: 4px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  font-size: 14px;
}
body .container #app .poll-container .history .items .row .cell {
  opacity: 0.8;
}
body .container #app .poll-container .history .items .row .cell .dim {
  opacity: 0.4;
}
body .container #app .poll-container .history .items .row .cell.mood {
  text-align: left;
}
body .container #app .poll-container .history .items .row .cell.postedAt {
  font-weight: 300;
}
body .container #app .poll-container .history .items .row .cell.address {
  font-weight: 300;
  text-align: right;
}
body .container #app .poll-container .history .items .empty {
  opacity: 0.2;
  font-size: 14px;
}